.row {
    display: flex;
    align-items: center;
    width: max-content;
    margin: var(--gap-s) auto;
    transition: transform 0.2s ease-in-out;
}

.item {
    max-width: 300px;
    margin-top: 0;
    margin-right: var(--gap-m);
    font-family: inherit;
    font-size: 20px;
    font-weight: bold;
    color: #333;
    white-space: nowrap;
    cursor: pointer;
    opacity: 0.5;
}

.itemActive {
    opacity: 1;
}